export default function IndexPage() {
  const [navbar, setNavbar] = useState(false);
  const titleSectionRef = useRef<HTMLDivElement>(null);

  useIntersectionObserver({
    refs: [titleSectionRef],
    options: useMemo(
      () => ({
        threshold: [1],
        rootMargin: `-${remToPx(1.5) + 1}px 0px 0px 0px`,
      }),
      []
    ),
    callback: useCallback(
      ([el]: IntersectionObserverEntryNarrow<HTMLDivElement>[]) =>
        setNavbar(el.intersectionRatio < 1),
      []
    ),
  });

  return (
    <>
      <Navbar links={links} className={clsx(!navbar && 'hidden')} />
      <main className="pt-[10rem] pb-[1.25rem] px-[1.25rem] text-[0.875rem] md:px-[2.5rem] md:pt-[13.75rem] md:pb-[2rem]">
        <h1
          ref={titleSectionRef}
          className="sticky top-[1.25rem] z-40 mb-[0.75rem] leading-[140%] font-medium md:mb-0 md:h-0"
        >
          Chico Araujo
        </h1>
        <section className="pb-[3.75rem] md:grid md:grid-cols-23 md:pb-[10rem]">
          <div className="md:col-start-7 md:col-span-12">
            <p className="mb-[0.75rem] font-display text-[2rem] leading-[120%] md:text-[4.25rem] md:leading-[110%] md:mb-[1.25rem]">
              Hands-on designer with over 10 years of experience creating
              digital products.
            </p>
            <p className="text-[#ababab] leading-[160%] mb-[2.5rem] md:mb-[1.25rem]">
              <span className="text-white">Currently</span> at Heron.{' '}
              <span className="text-white">Previously </span> at {` Work&Co `}
              and Huge where I had the opportunity to work for companies like
              Apple, Amazon, Goldman Sachs, Mailchimp, Meta, Twitter and many
              more.
            </p>
            <ul className="flex gap-[1.25rem] leading-[140%]">
              {links.map(({ text, href, target }) => (
                <li key={text}>
                  <Link href={href} target={target} rel="noreferrer noopener">
                    {text}
                  </Link>
                </li>
              ))}
            </ul>
          </div>
        </section>

        {cases.map((c) => (
          <CompanyCase key={c.name} data={c} className="mt-[6.25rem]" />
        ))}

        <BackToTop />
      </main>
    </>
  );
}

export const Head: HeadFC = () => <title>Chico Araujo</title>;
