import clsx from 'clsx';
import type { HTMLProps } from 'react';

export type PictureProps = HTMLProps<HTMLDivElement>;
export const Picture = ({ className, ...props }: PictureProps) => {
  return (
    <div
      className={clsx(
        className,
        'rounded-[0.5rem] overflow-hidden bg-[#323232]'
      )}
      {...props}
    />
  );
};
