export type BackToTopProps = {
  children?: never;
};

// eslint-disable-next-line @typescript-eslint/no-unused-vars
export const BackToTop = ({ children }: BackToTopProps) => {
  return (
    <div className="relative">
      <button
        type="button"
        className="absolute p-0 m-0 bottom-[0.5rem] border-none bg-none font-medium cursor-pointer max-md:hidden"
        onClick={() =>
          document.scrollingElement?.scrollTo({ top: 0, behavior: 'smooth' })
        }
      >
        Back to top
      </button>
    </div>
  );
};
