import clsx from 'clsx';
import type { HTMLProps } from 'react';

export type TagProps = HTMLProps<HTMLDivElement>;

export const Tag = ({ className, ...props }: TagProps) => {
  return (
    <div
      className={clsx(
        className,
        'px-[0.5625rem] py-[0.3125rem] rounded-[0.25rem] overflow-hidden bg-[#191919]'
      )}
      {...props}
    />
  );
};
