import clsx from 'clsx';
import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import type { HTMLProps } from 'react';
import type { IntersectionObserverEntryNarrow } from '../../hooks/use-intersection-observer';
import { useIntersectionObserver } from '../../hooks/use-intersection-observer';

const desktop = 1024;

export type SmartVideoProps = Omit<
  HTMLProps<HTMLVideoElement>,
  | 'src'
  | 'srcDoc'
  | 'srcLang'
  | 'srcSet'
  | 'ref'
  | 'loop'
  | 'muted'
  | 'disablePictureInPicture'
  | 'playsInline'
  | 'poster'
> & {
  eager?: boolean;
  srcMobile?: string;
  srcDesktop?: string;
};

export const SmartVideo = ({
  eager,
  srcMobile,
  srcDesktop,
  children,
  className,
  ...props
}: SmartVideoProps) => {
  const ref = useRef<HTMLVideoElement>(null);
  const [shouldLoad, setShouldLoad] = useState(eager);

  // hide the video element while loading
  useEffect(() => {
    const el = ref.current;
    if (!el) return;

    el.style.opacity = '0';
    const handler = () => {
      if (el.readyState >= 2) el.style.opacity = '1';
    };

    el.addEventListener('canplay', handler);
    return () => {
      el.removeEventListener('canplay', handler);
    };
  }, []);

  useIntersectionObserver({
    refs: [ref],
    options: useMemo(() => ({ rootMargin: '0% 0% 25% 0%' }), []),
    callback: useCallback(
      ([el]: IntersectionObserverEntryNarrow<HTMLVideoElement>[]) => {
        setShouldLoad((old) => old || el.intersectionRatio > 0);
      },
      []
    ),
  });

  useIntersectionObserver({
    refs: [ref],
    callback: useCallback(
      ([el]: IntersectionObserverEntryNarrow<HTMLVideoElement>[]) => {
        const onScreen = el.intersectionRatio > 0;

        try {
          if (onScreen && el.target.paused) el.target.play().catch(() => {});
          else if (!onScreen && !el.target.paused) el.target.pause();
        } catch {
          /* empty */
        }
      },
      []
    ),
  });

  const src = useMemo(
    () =>
      srcMobile && srcDesktop && srcMobile !== srcDesktop ? (
        <>
          <source src={srcMobile} media={`(max-width: ${desktop}px)`} />
          <source src={srcDesktop} media={`(min-width: ${desktop}px)`} />
        </>
      ) : (
        <source src={srcMobile ?? srcDesktop} />
      ),
    [srcDesktop, srcMobile]
  );

  return (
    <div className="w-full h-full relative">
      {children}
      <video
        {...props}
        className={clsx(className, 'relative')}
        ref={ref}
        loop
        muted
        disablePictureInPicture
        playsInline
        draggable="false"
      >
        {shouldLoad && src}
      </video>
    </div>
  );
};
