import clsx from 'clsx';
import { Link } from '../link';

const MAX_LINKS_XS = 2;

export type NavbarProps = {
  links: { href: string; text: string }[];
  className?: string;
};

export const Navbar = ({ links, className }: NavbarProps) => {
  return (
    <header
      className={clsx(
        className,
        'h-[3.75rem] p-[1.25rem] backdrop-blur-[10px] bg-black/70 flex justify-end fixed top-0 left-0 w-full z-40 text-[0.875rem] md:px-[2.5rem]'
      )}
      aria-label="Navigation bar"
    >
      <nav className="flex gap-[1.25rem]">
        {links.map(({ text, href }, i) => (
          <Link
            key={text}
            href={href}
            target="_blank"
            rel="noreferrer noopener"
            className={clsx(i >= MAX_LINKS_XS && 'max-md:hidden')}
          >
            {text}
          </Link>
        ))}
      </nav>
    </header>
  );
};
