import { useEffect } from 'react';

export type UseIntersectionObserverProps<E extends HTMLElement> = {
  refs: React.RefObject<E>[];
  callback: (
    entries: IntersectionObserverEntryNarrow<E>[],
    observer: IntersectionObserver
  ) => void;
  options?: IntersectionObserverInit;
};

export type IntersectionObserverEntryNarrow<E extends HTMLElement> = Omit<
  IntersectionObserverEntry,
  'target'
> & {
  target: E;
};

export function useIntersectionObserver<E extends HTMLElement>({
  refs,
  options,
  callback,
}: UseIntersectionObserverProps<E>) {
  useEffect(() => {
    const els = refs.filter((ref) => ref.current);
    if (els.length < 1) return;

    const observer = new IntersectionObserver(callback as never, options);

    for (const el of els) {
      if (el.current) observer.observe(el.current);
    }

    return () => observer.disconnect();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [callback, options]);
}
