import clsx from 'clsx';
import type { HTMLProps } from 'react';

export type CompanyLogoProps = HTMLProps<HTMLDivElement>;
export const CompanyLogo = ({ className, ...props }: CompanyLogoProps) => {
  return (
    <div
      className={clsx(
        className,
        'rounded-[0.25rem] overflow-hidden bg-[#191919] w-[2rem] h-[2rem]'
      )}
      {...props}
    />
  );
};
