import clsx from 'clsx';
import type { HTMLProps, ReactNode } from 'react';
import { CompanyLogo } from '../company-logo';
import { Picture } from '../picture';
import { Tag } from '../tag';

export type CompanyCaseData = {
  name: string;
  description: ReactNode;
  role: ReactNode;
  logo: ReactNode;
  tags: ReactNode[];
  pictures: ReactNode[];
};

export type CompanyCaseProps = Omit<
  HTMLProps<HTMLDivElement>,
  'children' | 'data'
> & {
  data: CompanyCaseData;
};

export const CompanyCase = ({
  className,
  data: { name, logo, description, role, tags, pictures },
  ...props
}: CompanyCaseProps) => {
  return (
    <section className={clsx(className, 'md:grid md:grid-cols-23')} {...props}>
      <div className="md:col-span-5 md:sticky md:top-[6.25rem] md:self-start">
        <div className="flex items-center gap-[0.75rem] mb-[1rem]">
          <CompanyLogo>{logo}</CompanyLogo>
          <h2 className="font-medium">{name}</h2>
        </div>
        <p className="mb-[0.75rem] leading-[140%]">
          <span>About:</span>
          <span className="text-[#ababab]">{' ' + description}</span>
        </p>
        <p className="mb-[1rem] leading-[140%]">
          <span>My role</span>
          <span className="text-[#ababab]">{' ' + role}</span>
        </p>
        <div className="flex flex-wrap gap-[0.25rem] mb-[2.5rem]">
          {tags.map((tag, i) => (
            <Tag key={`tag-${i}`}>{tag}</Tag>
          ))}
        </div>
      </div>
      <div className="grid grid-cols-1 auto-rows-fr gap-[0.625rem] md:col-start-7 md:col-span-17 md:grid-cols-2">
        {pictures.map((pic, i) => (
          <Picture
            key={`pic-${i}`}
            className={clsx(i % 3 === 0 && 'md:col-span-2')}
          >
            {pic}
          </Picture>
        ))}
      </div>
    </section>
  );
};
